import './SideBarRight.css';
import { BlockSideBarList } from '../windows/BlockSideBarList/BlockSideBarList';
import { Inventory } from '../Inventory/Inventory';
import { Dispatch, SetStateAction, useState } from 'react';
import { BlockSideBarSkin } from '../windows/BlockSideBarSkin/BlockSideBarSkin';

export const SideBarRight = ({
    isInventoryOpen,
    setIsInventoryOpen,
}: {
    isInventoryOpen: boolean;
    setIsInventoryOpen: Dispatch<SetStateAction<boolean>>;
}) => {
    return (
        <aside className="side-bar-right">
            {isInventoryOpen ? (
                <Inventory setIsInventoryOpen={setIsInventoryOpen} />
            ) : (
                <div className="side-bar-right__container">
                    <BlockSideBarList />
                    <BlockSideBarList />
                    <BlockSideBarList />
                    <BlockSideBarSkin />
                </div>
            )}
        </aside>
    );
};
